import React from 'react'
import styled from 'styled-components'
import { Link } from 'gatsby'
import renciLogo from '../../images/renci.png'

export const BrandContainer = styled(Link).attrs({ to: '/' })(({ theme }) => `
    padding: 0 1rem;
    height: 100%;
    flex: 1;
    display: flex;
    align-items: center;
`)

export const Brand = ({ path = '/', height = 'auto', width = '150px', style }) => {
    return (
        <BrandContainer to={ path } style={{ ...style, maxWidth: width }}>
            <img src={ renciLogo } height={ height } width={ width } alt="RENCI logo" />
        </BrandContainer>
    )
}