import React from 'react'
import styled, { useTheme } from 'styled-components'
import PropTypes from 'prop-types'
import { Header } from './header'
import { Brand } from '../../brand'
import { Main } from './main'
import { Footer } from './footer'
import { Container } from '../container'
import { Menu, MobileMenu } from '../../menu'
import { useLayoutContext } from '../../../contexts'
import { useMenu } from '../../../hooks'
import { Container as Grid, Row, Col } from 'react-grid-system'
import { Paragraph } from '../../typography'
import { ExternalLink } from '../../link'
import { UnorderedList, ListItem } from '../../list'
import { FacebookIcon, LinkedInIcon, TwitterIcon } from '../../icons'
import '../../../styles/normalize.css'
import '../../../styles/customize.css'

const SocialLink = styled(ExternalLink)`
    margin: 0 1rem;
    transition: filter 250ms;
    &:hover {
        filter: brightness(1.5);
    }
`

const LayoutWrapper = styled.div(({ theme }) => `
    background-color: #f9fafc;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background: linear-gradient(150deg, #ffffff00, ${ theme.color.renciBlue }99 100vmax),
                linear-gradient(170deg, ${ theme.color.carolinaBlue }33, #ffffff00 100vmax);
`)

export const DefaultLayout = ({ children }) => {
    const theme = useTheme()
    const menuItems = useMenu()
    const { isCompact } = useLayoutContext()
    return (
        <LayoutWrapper>
            <Header compact={ isCompact }>
                <Brand width="150px"/>
                { isCompact ? <MobileMenu items={ menuItems} /> : <Menu items={ menuItems} /> }
            </Header>
            <Main>
                { children }
            </Main>
            <Footer>
                <Container style={{ padding: '2rem 0' }}>
                    <Grid fluid>
                        <Row>
                            <Col xs={ 12 } md={ 6 }>
                                <Paragraph>
                                    &copy; <a href="https://www.renci.org/" target="_blank" rel="noopener noreferrer">RENCI</a> { new Date().getFullYear() }
                                </Paragraph>
                            </Col>
                            <Col xs={ 12 } md={ 6 }>
                                <UnorderedList style={{ listStyleType: 'none', textAlign: 'right' }}>
                                    <ListItem primary={ <a href="https://www.renci.org/" target="_blank" rel="noopener noreferrer">RENCI</a> } />
                                    <ListItem primary={ <a href="/">Quos magni</a> } />
                                    <ListItem primary={ <a href="/">Possimus</a> } />
                                    <ListItem primary={ <a href="/">Maiores</a> } />
                                </UnorderedList>
                            </Col>
                        </Row>
                    </Grid>
                    <Row>
                        <Col xs={ 12 }>
                            <Paragraph center>
                                <SocialLink to="https://twitter.com/RENCI">
                                    <TwitterIcon size={ 32 } fill={ theme.color.primary.light } />
                                </SocialLink>
                                <SocialLink to="https://www.facebook.com/renci.org/">
                                    <FacebookIcon size={ 32 } fill={ theme.color.primary.light } />
                                </SocialLink>
                                <SocialLink to="https://www.linkedin.com/company/renaissance-computing-institute/">
                                    <LinkedInIcon size={ 32 } fill={ theme.color.primary.light } />
                                </SocialLink>
                            </Paragraph>
                        </Col>
                    </Row>
                </Container>
            </Footer>
        </LayoutWrapper>
    )
}

DefaultLayout.propTypes = {
    children: PropTypes.node.isRequired,
}
